export default function keepFocused () {
    const items:NodeListOf<Element> = document.querySelectorAll('[data-input-keep-focused]');

    for (let i = 0; i < items.length; i++) {
        items.item(i).addEventListener('input', function () {
            this.value ? this.classList.add('active') : this.classList.remove('active');
        });
    }
}